type TDiscount = {
  cover: string;
  storeName: string;
  discountText: string;
};

type TProps = {
  discounts: TDiscount[];
};

export function Discount({ data }: { data: TDiscount }) {
  return (
    <div className="container">
      {data.discountText}
      <style jsx>{`
        .container {
          display: flex;
          align-items: center;
          color: #7F7F7F;
          padding: 12px 0;
          border-bottom: solid 1px;
        }
      `}</style>
    </div>
  );
}

export default function Discounts({ discounts }: TProps) {
  return (
    <div className="container">
      {discounts.map((discount, idx) => (
        <Discount key={`${idx}: ${discount.storeName}`} data={discount} />
      ))}
      <style jsx>{`
        .container {
          margin: 16px 0;
          padding: 16px;
          border-radius: 10px;
          background-color: white;
        }
      `}</style>
    </div>
  );
}
